<template>
  <div class="todo-wrapper">
      <h1>Mida täna vaja teha on?</h1>
      <p v-if="this.todos.length > 0">Ülesandeid veel: {{ this.todos.filter(t => !t.completed).length  }}</p>
      <button class="btn" v-on:click="archive">Arhiivi</button>
      <AddTodo v-on:add-todo="addTodo"/>
      <div class="todos" v-bind:key="todo.id" v-for="todo in todos">
        <TodoItem v-bind:todo="todo"/>
      </div>
  </div>
</template>

<script>
import TodoItem from './TodoItem.vue';
import AddTodo from './AddTodo.vue';

export default {
  name: 'todo',
  components: {
    TodoItem,
    AddTodo
  },
  props: ["todos"],
  methods: {
    addTodo(newTodo) {
      this.todos = [...this.todos, newTodo]
    },
    archive() {
      this.todos = this.todos.filter(todo => !todo.completed);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

h1 {
    color: #000;
    margin: 1rem 0;
  }

  p {
    margin-bottom: 1rem;
  }

  .todo-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }

  #input {
    padding: .3rem;
    height: 2rem;
  }

  #addItem {
    outline: none;
    border: none;
    background: #42b983;
    padding: .8rem 2rem;
    margin: 0 .5rem;
    text-transform: uppercase;
    font-size: 1.2rem;
  }

  .btn {
    display: inline-block;
    border: none;
    background: #27a883;
    color: #fff;
    padding: 7px 20px;
    cursor: pointer;
    margin-bottom: 1rem;
    text-transform: uppercase;
  }

  .btn:hover {
    background: #0fd89e;
  }


</style>
